<template>
  <div v-if="!currentDevices">LOADING</div>
  <div v-else-if="currentDevices">
    <BaseHeader :current-device="this.currentDevices" />
    <main class="devices-page">
      <h2 class="devices-page__title">Выберите модель ультразвукового сканера Mindray</h2>
      <div class="devices-page__filter-wrap" v-if="filter" @click.stop="toggleFilterWrapActive">
        <ul class="devices-page__filter price-filter">
          <li class="price-filter__item"  v-for="option in filters" :key="option.label" :class="{choosen : option.label === choosenCategory}" >
            <label class="price-filter__label">
              <input class="price-filter__input visually-hidden" type="radio" :value="option.label" v-model="choosenCategory">
              {{ option.label }} {{ option.amount }}            
            </label>
            <button class="price-filter__info"  @click="showTip">
                <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" viewBox="0 0 25 25" fill="transparent"><circle cx="12.5" cy="12.5" r="12" stroke="#BDC8D3"/><path d="M11.5615 9.84514H13.6518V19.5032H11.5615V9.84514ZM11.2905 6.76772C11.2905 6.40643 11.4067 6.12901 11.6389 5.93546C11.8712 5.74192 12.1873 5.64514 12.5873 5.64514C13.026 5.64514 13.355 5.74192 13.5744 5.93546C13.8067 6.12901 13.9228 6.40643 13.9228 6.76772C13.9228 7.14192 13.8067 7.43224 13.5744 7.63869C13.355 7.83224 13.026 7.92901 12.5873 7.92901C12.1873 7.92901 11.8712 7.83224 11.6389 7.63869C11.4067 7.43224 11.2905 7.14192 11.2905 6.76772Z" fill="#BDC8D3"/></svg>
                <span class="price-filter__descr">{{ option.description }}</span>
            </button>
          </li>
        </ul>
      </div>
      <ul class="devices-page__devices-list" v-if="currentDevices.models">
        <li class="devices-page__devices-item" v-for="item in choosenModels" :key="item.id" >
          <DeviceItem :model="item" />
        </li>
      </ul>
      <h3 class="error-message" v-else> СПИСОК ТОВАРОВ НЕДОСТУПЕН</h3>
    </main>
  </div>
</template>  

<script>
  import devicesList from '@/mixins/devices';
  import BaseHeader from '@/components/BaseHeader';
  import DeviceItem from '@/components/DeviceItem';

  export default {
    name: 'DevicesPage',
    components: {BaseHeader, DeviceItem},
    mixins: [devicesList],
    data() {
      return {
        currentDevices: {},
        filter: [],
        choosenCategory: 'Все',
      }
    },
    computed: {     
      choosenModels() {
        if(this.choosenCategory === 'Все') {
          return this.currentDevices.models
        } else {
          return this.currentDevices.models.filter(item => item.priceCategory === this.choosenCategory)
        }
      }
    },
    methods: {
      loadDevices(){
        try {
          this.currentDevices = this.devices.find(item => item.path === this.$route.params.title)
        }
        catch {
          console.log('no currentDevices')
        }
      },
      getFilters(){
        if (this.currentDevices) {
          return [
            {
              label: 'Все',
              amount: this.currentDevices.models.length,
              description:'Широкий ассортимент аппаратов'
            },
            {
              label: 'Бюджетный',
              amount: this.currentDevices.models.filter(item => item.priceCategory === 'Бюджетный').length,
              description:'Бюджетные аппараты по приятной цене'
            },
            {
              label: 'Средний',
              amount: this.currentDevices.models.filter(item => item.priceCategory === 'Средний').length,
              description:'Универсальные аппараты с улучшенным качеством визуализации'
            },
            {
              label: 'Высокий',
              amount: this.currentDevices.models.filter(item => item.priceCategory === 'Высокий').length,
              description:'Лучшие аппараты с отличным качеством визуализации'
            },
            {
              label: 'Экспертный',
              amount: this.currentDevices.models.filter(item => item.priceCategory === 'Экспертный').length,
              description:'Лучшие аппараты непревзойдённого качества'
            }
          ]
        } else {
          return []
        }
      },
      showTip(event){
        const activeTipBtn = document.querySelector('.price-filter__info.active');        

        activeTipBtn?.classList.remove('active')
        if(event.target !== activeTipBtn) {
          event.target.classList.add('active')
        }
      },
      toggleFilterWrapActive(e){
        console.log(e.target);
        const filterWrap = document.querySelector('.devices-page__filter-wrap');

        if (!e.target.classList.contains('price-filter__input') && !e.target.classList.contains('price-filter__info')) {
          filterWrap.classList.toggle("active");
        }        
      }
    },
    watch: {
      '$route.params.devices':{
        handler(){
          this.loadDevices()
        },
        immediate: true //замена хука created()
      }
    },
    created(){
      this.filters = this.getFilters()
    },
    mounted(){
      document.body.addEventListener('click', (e) => {
        const activeDropDown = document.querySelector('.devices-page__filter-wrap.active');
        if(activeDropDown && !e.target.classList.contains('price-filter__info')) {
          activeDropDown.classList.remove("active");
        }
      })
    }
  }  
</script>

<style lang="scss">
  .devices-page {
    display: grid;
    justify-items: center;
    gap: 70px;
    margin-bottom: 200px;
    padding-top: 70px;

    &__title {
      justify-self: center;
      width: Min(610px, 100%);
      font: 350 36px/1.2 'Normalidad Text';
      text-align: center;
    }

    &__devices-list {
      max-width: 1242px;
      width: 100%;
      display: flex;
      flex-wrap: wrap;     
    }

    &__devices-item {
      width: calc((100% -  90px) / 4);      
      display: flex;
      margin-bottom: 90px;
      &:not(:nth-child(4n)) {
        margin-right: 30px;
      }
      & img {
        flex-grow: 1;
      }      
    }
  }

  .price-filter {
    position: relative;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(5, max-content);
    column-gap: 25px;

    &__item {
      position: relative;
      width: max-content;
      display: flex;
      align-items: center;
      padding:  20px 15px;
      border: 1px solid transparent;
      border-radius: 10px;
      transition: .25s ease-in-out;
      transition-property: color, background-color;

      &.choosen {
        background-color: var(--black);
        color: var(--white);
      }
    }

    &__label {
      font-size: 20px;
      line-height: 120%;
    }

    &__info {
      position: relative;
      margin-left: 10px;
      &:not(.active) .price-filter__descr {
        opacity: 0;
        pointer-events: none;
      }
      &.active svg{
        fill: var(--black)
      }
      svg {
        pointer-events: none;
        transition: fill .25s ease-in-out;
      }
    }

    &__descr {
      position: absolute;
      bottom: 55px;
      left: 0;
      display: block;
      width: max-content;
      max-width: 430px;
      padding: 16px 20px;
      background: var(--white);
      border: 1px solid var(--lightgrey);
      border-radius: 10px;
      transform: translateX(Calc((25px - 100%) / 2));
      font-size: 18px;
      line-height: 140%;
      color: var(--black);
      transition: opacity .25s ease-in-out;

    }

  }

  /* Эффекты */
  @media screen and (any-hover: hover) {
    .model:hover {
      outline: var(--lightgrey) 1px solid;      
    }
  }

  .model:focus-visible {
    outline: var(--black) 1px solid;
  }

  /* MEDIA */

  @media (max-width: 1480px) {
    .devices-page {
      margin-bottom: 70px;

      &__devices-item {
        width: calc((100% -  50px) / 3); 
        margin-bottom: 70px;
        &:nth-child(n) {
          margin-right: 25px;
        }
        &:nth-child(3n) {
          margin-right: 0;
        }
      }
    }

    .price-filter__item {
      &:nth-child(1), &:nth-child(5){
        .price-filter__info {
          position: static;
        }
        .price-filter__descr  {
          bottom: 74px;
          transform: none;
        }
      }
      &:nth-child(5){
        .price-filter__descr  {
          left: auto;
          right: 0;
        }
      } 
    }
  }

  @media (max-width: 1200px) {
    .price-filter {
      grid-template-columns: repeat(6, max-content);

      &__item{
        grid-column: span 2;
        &:nth-child(4) {
          grid-column: 2 / span 2;
        }

        &:nth-child(2), &:nth-child(3), &:nth-child(4){
          .price-filter__info {
            position: static;
          }
          .price-filter__descr  {
            bottom: 74px;
            transform: none;
          }
        }  
        &:nth-child(3){
          .price-filter__descr  {
            left: auto;
            right: 0;
          }
        }  
      }
    }
  }
  
  @media (max-width: 752px) {
    .devices-page {
      gap: 40px;
      margin-bottom: 40px;

      &__title {
        font-size: 24px;
      }

      &__devices-item {
        width: calc((100% -  20px) / 2);
        margin-bottom: 40px;
        &:nth-child(odd) {
          margin-right: 20px;
        }
        &:nth-child(even) {
          margin-right: 0;
        }   
      }

      &__filter-wrap {
        position: relative;
        width: 100%;
        height: 94px;

        &::before, &::after {
          position: absolute;
          z-index: 2;
          content: '';
          top: 30px;
          width: 20px;
          height: 2px;
          background-color: var(--black);
          border-radius: 1px;
          transition: transform .25s ease-in-out;
        }
        &::before {
          right: 30px;
          transform: rotate(-45deg);
        }
        &::after {
          right: 43px;
          transform: rotate(45deg);
        }

        &.active {
          &::before {
            right: 15px;
            transform: translateX(-14px) rotate(-45deg);
          }
          &::after {
            right: 30px;
            transform: translateX(14px) rotate(45deg);
          }
        }

        .price-filter {
          position: absolute;
          padding: 8px 15px;
          width: 100%;
          border: 1px solid var(--lightgrey);
          border-radius: 10px;
          background-color: var(--white);
        }


        &:not(.active) {  
          .price-filter__item {
            &:not(.choosen) {
              position: absolute;
              visibility: hidden;
              width: 1px;
              height: 1px;
              padding: 0;
              margin: -1px;
              margin: 0;
              opacity: 0;
              pointer-events: none;
            }
          }
        }
      }
    }

    .price-filter {
      grid-template-columns: repeat(2, max-content);
      &__item{
        &:not(:first-child) {
          justify-self: center;
        }   
        &:nth-child(n){
          .price-filter__descr  {
            bottom: 50px;
          }
        } 
        &:not(:first-child) {
          justify-self: start;
          grid-column: span 1;
        }
        padding: 9px 12px;

        &__label {
          font-size: 18px;
        }
      }
    }
  }

  @media (max-width: 534px) {
    .devices-page {
      padding-top: 40px;

      &__title {
        max-width: calc(100vw - 40px);
        font-size: 18px;
      }

      &__devices-item {
        width: 100%;
        margin-bottom: 40px;
        &:nth-child(odd) {
          margin-right: 20px;
        }
        &:nth-child(even) {
          margin-right: 0;
        }   
      }
    }

    .price-filter {
      justify-self: start;
      grid-template-columns: 100%;
      gap: 0;
      &__item{
        &:first-child {
          grid-column: span 1;
        }
        &:nth-child(n) {
          justify-self: start;
        }

        &:nth-child(n){
          .price-filter__descr  {
            left: 0;
            right: auto;
            bottom: 39px;
            max-width: calc(100vw - 40px);
            font-size: 14px;
            line-height: 120%;
          }
        } 
      }

      &__label {
        font-size: 14px;
      }

      &__info {
        margin-left: 7px;
        svg {          
          width: 18px;
          height: 18px;
        }
      }

    }
  }

  @media (max-width: 320px) {
    .devices-page {      
      &__title {
        text-align: start;
      }
    }
  }
</style>