<template>
  <a class="model">
    <picture class="model__pic">
      <img class="model__img" :src="getImg()">
    </picture>
    <h3 class="model__title">{{ model.title }}</h3>
    <span class="model__price">{{ model.price | numberFormat }} ₽</span>
  </a>
</template>

<script>
  import numberFormat from '@/helpers/numberFormat';

  export default {
    name: 'device-item',
    props: {
      model: Object
    },
    methods: {
      getImg(){
        return this.model.imgUrl ? `img/${this.model.imgUrl}.jpg` : 'img/no-img.svg'
      }
    },
    filters: {
      numberFormat
    },
  }
</script>

<style lang="scss">
  .model {
    margin: 0 auto;
    display: grid;
    grid-template-rows: auto max-content max-content;
    row-gap: 15px;
    outline-offset: 6px;
    
    &__title {
      font: 700 28px/1.2 'Normalidad Wide'
    }

    &__price {
      font-size: 28px;
      line-height: 120%;
    }
  }

  /* MEDIA */

  @media (max-width: 534px) {
    .model {      
      &__title {
        font-size: 20px;
        margin-bottom: -5px;
      }

      &__price {
        font-size: 20px;
      }
    }
  }
</style>