<template>
<div class="homepage">
  <BaseHeader />  
  <main class="homepage__content">
    <h1 class="main-title">Конфигуратор медицинского оборудования</h1>
    <h2 class="homepage__instruction">Подберите датчики, принтеры, шнуры, програмное&nbsp;обеспечение и&nbsp;обслуживание для медицинского аппарата</h2>
    <ul class="homepage__devices">
      <li v-for="device in devices" :key="device.path">
        <router-link class="homepage__device-link" :to="{name: 'devices', params: {title: device.path}}" href="#" :style="`background-color:${device.bgColor}`" >
          <img class="homepage__device-img" :src="`img/${device.path}.svg`" :alt="device.title">
          <h3 class="homepage__device-title">{{ device.title }}</h3>
        </router-link>        
      </li>
    </ul>
  </main>
</div>
  
</template>

<script>
  import BaseHeader from '@/components/BaseHeader';
  import devicesList from '@/mixins/devices';

	export default{
    name: 'homePage',
    components: {BaseHeader},
    mixins: [devicesList],
	}
</script>

<style lang="scss">
  .homepage {
    .header {
      padding: 35px 80px;
      background: transparent;

      &__container {
        &::before {
          display: none;
        }
      }


      &__logo {
        padding: 0;
        margin-right: auto;
        background: transparent;
        color: var(--logo-red);
        &::after {
          display: none;
        }
      }

      &__complects-link {
        color: var(--blue);
      }
      &__authorization {
        color: var(--black);
      }
    }
  }

  .main-title {
    justify-self: center;
    max-width: 1040px;
    font: 700 48px/1.2 'Normalidad Wide';
    text-align: center;
  }

  .homepage{
    margin-bottom: 80px;

    &__content {
      padding-top: 15px;
      width: 100%;
      display: grid;
      row-gap: 20px;      
    }

    &__instruction {
      width: Min(100%, 508px);      
      justify-self: center;
      margin-bottom: 50px;
      font: 350 24px/1.2 'Normalidad Wide';
      text-align: center;
    }

    &__devices {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
    }

    &__device-link {
      position: relative;
      display: grid;
      grid-template-rows: max-content;
      gap: 55px;
      justify-content: center;
      height: 100%;
      width: 100%;
      padding: 150px 50px 123px;      
      transition: border-color .35s ease-in-out;
      &::before, &::after {
        content: '';
        position: absolute;
        inset: 0;
      }
      &::before {
        border: 4px solid transparent;
        transition: border-color .35s ease-in-out;
      }
      &::after {
        background-color: transparent;
        border: 4px solid transparent;
        transition: background-color .35s ease-in-out;
        opacity: 0.35;
      }
      &:focus-visible::before {
        border-color: var(--white)
      }
    }

    &__device-img {
      display: inline-block;
      margin: 0 auto;
      min-height: 130px;
    }

    &__device-title {
      
      max-width: 350px;
      color: var(--white);
      text-align: center;
      font: 500 56px/1 "Normalidad Text"
    }
  }

  
  .header__complects-link, .header__authorization {
    position: relative;
    &::after {
      position: absolute;
      content: '';
      left: 0;
      right: 0;
      bottom: -4px;
      height: 2px;
      transform: scaleX(0);
      transition: transform .25s ease-in-out;
      transform-origin: left;
      background-color: currentColor;
    }
    &:focus-visible {
      color: var(--deep-blue);
    }
  }

  /* Эффекты */
  @media screen and (any-hover: hover) {
    .header__complects-link:hover::after, .header__authorization:hover::after {
      transform: scaleX(1);
    }

    .homepage__device-link:hover::after {
      background-color: var(--white);
    }
  }

  /* MEDIA */

  @media (max-width: 1480px) {
    .main-title {
      font-size: 38px;
    }

    .homepage {
      .header {
        padding: 35px 0;
      }
        
      &__devices {
        margin: 0 -50px;
        width: calc(100% + 100px);
        grid-template-columns: repeat(2, 1fr);
      }

      &__device-link {
        padding: 75px 50px;
      }
    }
  }

  @media (max-width: 752px) {
    .homepage {
      .header {
        padding: 22px 0;
      }
    }

    .homepage {      
      margin-bottom: 70px;
      
      &__content {
        padding-top: 28px;
      }

      &__instruction {
        margin-bottom: 50px;
        font-size: 18px;
        line-height: 140%;
      }

      &__devices {
        margin: 0 -35px;
        width: calc(100% + 70px);
        grid-template-columns: 1fr;
      }

      &__device-link {
        grid-template-columns: 25% 1fr;        
        column-gap: 34px;
        justify-content: start;
        align-items: center;
        padding: 50px 35px;
      }

      &__device-img {
        justify-self: center;
        min-height: unset;
        max-height: 57px;
        margin-bottom: 0;
      }

      &__device-title {
        font-size: 42px;
        text-align: start;
      }
    }
  }

  @media (max-width: 534px) {
    .main-title, .homepage__device-title{
      font-size: 28px;
    }

    .homepage {
      &__devices {
        margin: 0 -20px;
        width: calc(100% + 40px);
      }

      &__device-link {
        padding: 27px 20px;
      }

    }
  }

  @media (max-width: 320px) {
    .homepage {
      .main-title, &__instruction {
        justify-self: start;
        text-align: start;
      }
    }
  }
</style>