<template>
  <header class="header">
    <div class="header__container">
      <a class="header__logo" href="" >
        <svg fill="currentColor">
          <use href="#header-logo"></use>
        </svg>
      </a>
      <div class="header__menu menu display-none">
        <ul class="menu__devices mobile-screen">
          <li class="menu__item" v-for="device in devices" :key="device.path">
            <router-link class="menu__device-link" :to="{name: 'devices', params: {title: device.path}}" href="#">
              {{ device.title }}
            </router-link>
          </li>
        </ul>
        <a class="header__complects-link" href='#'>Готовые&nbsp;комплекты</a>
        <button class="header__authorization">Вход</button>
        <div class="phone-block mobile-screen">
          <a class="phone-block__link" href="tel:+74994954821">+7 (499) 495-48-21</a>
          <span class="phone-block__text">Обратный&nbsp;звонок</span>
        </div>
      </div>
      <button class="burger mobile-screen" :class="{active : isBurgerOn, untouchable : isBurgerActive}" @click="switchBurger">
        <span class="burger__line"></span>
        <span class="burger__line"></span>
        <span class="burger__line"></span>
      </button>
    </div>
    <div class="header__device-wrap" v-if="currentDevice">
      <div class="header__device">      
        <img class="header__device-img" :src="`img/${currentDevice.path}.svg`" :alt="currentDevice.title">
        <h2 class="header__device-title">{{ currentDevice.title }}</h2>
      </div>
    </div>
  </header>
</template>

<script>
  import devicesList from '@/mixins/devices';
  export default {
    name: 'BaseHeader',
    mixins: [devicesList],
    props: {
      currentDevice: Object
    },
    data() {
      return {
        isBurgerOn: false,
        isBurgerActive: false,
      }
    },
    methods: {
      setDeviceBlockColor(){
        const headerDeviceElement = document.querySelector('.header__device-wrap');
        headerDeviceElement?.style.setProperty('--device-color', this.currentDevice.bgColor)
      },
      switchBurger(){
        this.isBurgerActive = true;
        window.setTimeout(() => {
          this.isBurgerActive = false;
        }, 500);
        this.isBurgerOn = !this.isBurgerOn;

        const menu = document.querySelector('.menu');

        if (this.isBurgerOn) {
          menu.classList.remove('display-none')        
        } else {
          setTimeout(() => menu.classList.add('display-none'), 500)
        }
        setTimeout(() => menu.classList.toggle('fade-active'), 1)      
      }
    },
    mounted(){
      this.setDeviceBlockColor();
    }
  }
</script>

